import { useState, useEffect } from 'react';

interface DaycareReservation {
  id: string;
  petName: string;
  ownerName: string;
  time: string;
  phone: string;
  status: 'confirmed' | 'pending' | 'completed' | 'cancelled';
}

interface DaycareData {
  [date: string]: DaycareReservation[];
}

/**
 * ë°ì´ì¼€ì–´ ì˜ˆì•½ ë‹¬ë ¥ ì»´í¬ë„ŒíŠ¸
 * - localStorageì— ì €ì¥ëœ daycareReservations ë°ì´í„°ë¥¼ ë¶ˆëŸ¬ì™€ ë‚ ì§œë³„ë¡œ ê·¸ë£¹í™”í•˜ì—¬ ë‹¬ë ¥ì— í‘œì‹œí•©ë‹ˆë‹¤.
 * - í‰ì¼ë§Œ ìš´ì˜ë˜ë©°, ì„ íƒí•œ ë‚ ì§œì˜ ì˜ˆì•½ ëª©ë¡ì„ ë³´ì—¬ì£¼ê³  ìˆ˜ë™ìœ¼ë¡œ ì¶”ê°€/ì‚­ì œí•  ìˆ˜ ìˆìŠµë‹ˆë‹¤.
 */
export default function DaycareCalendar() {
  const [currentDate, setCurrentDate] = useState(new Date());
  const [daycareData, setDaycareData] = useState<DaycareData>({});
  const [selectedDate, setSelectedDate] = useState<string | null>(null);
  // ì˜ˆì•½ ì¶”ê°€ í¼ í‘œì‹œ ì—¬ë¶€
  const [showAddForm, setShowAddForm] = useState(false);
  // ìƒˆ ë°ì´ì¼€ì–´ ì˜ˆì•½ ì…ë ¥ ê°’ ìƒíƒœ
  const [newReservation, setNewReservation] = useState({
    petName: '',
    ownerName: '',
    time: '',
    phone: ''
  });

  useEffect(() => {
    loadDaycareData();
    // ì‹¤ì‹œê°„ ì˜ˆì•½ ì—…ë°ì´íŠ¸ ê°ì§€
    const handleReservationUpdate = () => {
      loadDaycareData();
    };
    window.addEventListener('reservationUpdated', handleReservationUpdate);
    return () => {
      window.removeEventListener('reservationUpdated', handleReservationUpdate);
    };
  }, [currentDate]);

  const loadDaycareData = () => {
    try {
      // ë°ì´ì¼€ì–´ ì˜ˆì•½ì€ allReservationsì—ì„œ serviceê°€ 'daycare'ì¸ ê²ƒê³¼ daycareReservations ëª¨ë‘ë¥¼ í¬í•¨
      const allRaw = localStorage.getItem('allReservations');
      const daycareRaw = localStorage.getItem('daycareReservations');
      let reservations: any[] = [];
      if (allRaw) {
        try {
          const all = JSON.parse(allRaw);
          const fromAll = all.filter((r: any) => r && r.service === 'daycare');
          reservations = reservations.concat(fromAll);
        } catch (e) {
          console.warn('allReservations íŒŒì‹± ì‹¤íŒ¨:', e);
        }
      }
      if (daycareRaw) {
        try {
          const fromDaycare = JSON.parse(daycareRaw);
          reservations = reservations.concat(fromDaycare);
        } catch (e) {
          console.warn('daycareReservations íŒŒì‹± ì‹¤íŒ¨:', e);
        }
      }
      if (!reservations.length) {
        generateMockDaycareData();
        return;
      }
      const seenIds = new Set<string>();
      const deduped: any[] = [];
      for (const res of reservations) {
        if (!res || !res.id) continue;
        if (seenIds.has(res.id)) continue;
        seenIds.add(res.id);
        deduped.push(res);
      }
      const groupedData: DaycareData = {};
      deduped.forEach((reservation: any) => {
        const date = reservation.date;
        if (!date) return;
        if (!groupedData[date]) {
          groupedData[date] = [];
        }
        groupedData[date].push({
          id: reservation.id,
          petName: reservation.petName,
          ownerName: reservation.ownerName || 'ê³ ê°',
          time: reservation.time,
          phone: reservation.phone,
          status: reservation.status,
        });
      });
      Object.keys(groupedData).forEach(date => {
        groupedData[date].sort((a, b) => a.time.localeCompare(b.time));
      });
      setDaycareData(groupedData);
    } catch (error) {
      console.warn('ë°ì´ì¼€ì–´ ì˜ˆì•½ ë°ì´í„° ë¡œë“œ ì‹¤íŒ¨:', error);
      generateMockDaycareData();
    }
  };

  // ëª¨ì˜ ë°ì´ì¼€ì–´ ë°ì´í„° ìƒì„±
  const generateMockDaycareData = () => {
    const data: DaycareData = {};
    const year = currentDate.getFullYear();
    const month = currentDate.getMonth();
    const daysInMonth = new Date(year, month + 1, 0).getDate();
    const timeSlots = ['08:00', '09:00', '10:00', '11:00', '13:00', '14:00', '15:00', '16:00', '17:00'];
    const petNames = ['ì´ˆì½”', 'ë°”ë‘‘ì´', 'ë½€ì‚', 'ì½”ì½”', 'ëª½ì´', 'ë£¨ë£¨', 'ë³´ë¦¬', 'ì½©ì´', 'ë§ˆë£¨', 'í•˜ëŠ˜'];
    const ownerNames = ['ê¹€ë¯¼ìˆ˜', 'ì´ì˜í¬', 'ë°•ì² ìˆ˜', 'ì •ìˆ˜ì§„', 'ìµœë™í›ˆ', 'í•œì§€ë¯¼', 'ì†¡ë¯¼í˜¸', 'ìœ¤ì„œì—°', 'ì¥ë¯¸ì˜', 'ì˜¤ì¤€ì„'];
    for (let day = 1; day <= daysInMonth; day++) {
      const date = new Date(year, month, day);
      const dateString = date.toISOString().split('T')[0];
      // ì£¼ë§ ì œì™¸ (ë°ì´ì¼€ì–´ëŠ” í‰ì¼ë§Œ ìš´ì˜)
      if (date.getDay() === 0 || date.getDay() === 6) continue;
      const maxReservations = 6;
      const numReservations = Math.floor(Math.random() * maxReservations);
      if (numReservations > 0) {
        const dayReservations: DaycareReservation[] = [];
        const usedTimes = new Set<string>();
        for (let i = 0; i < numReservations; i++) {
          let time;
          do {
            time = timeSlots[Math.floor(Math.random() * timeSlots.length)];
          } while (usedTimes.has(time));
          usedTimes.add(time);
          dayReservations.push({
            id: `daycare-${dateString}-${i}`,
            petName: petNames[Math.floor(Math.random() * petNames.length)],
            ownerName: ownerNames[Math.floor(Math.random() * ownerNames.length)],
            time: time,
            phone: `010-${Math.floor(Math.random() * 9000) + 1000}-${Math.floor(Math.random() * 9000) + 1000}`,
            status: ['confirmed', 'pending', 'completed'][Math.floor(Math.random() * 3)] as any
          });
        }
        dayReservations.sort((a, b) => a.time.localeCompare(b.time));
        data[dateString] = dayReservations;
      }
    }
    setDaycareData(data);
  };

  const getDaysInMonth = () => {
    const year = currentDate.getFullYear();
    const month = currentDate.getMonth();
    const firstDay = new Date(year, month, 1);
    const lastDay = new Date(year, month + 1, 0);
    const daysInMonth = lastDay.getDate();
    const startingDayOfWeek = firstDay.getDay();
    const days: (number | null)[] = [];
    for (let i = 0; i < startingDayOfWeek; i++) {
      days.push(null);
    }
    for (let day = 1; day <= daysInMonth; day++) {
      days.push(day);
    }
    return days;
  };

  // ë‚ ì§œ ë¬¸ìì—´ì„ ë¡œì»¬ ì‹œê°„ ê¸°ì¤€(í•œêµ­ ì‹œê°„)ìœ¼ë¡œ ë°˜í™˜í•˜ëŠ” í•¨ìˆ˜
  // toISOString()ì€ UTC ê¸°ì¤€ìœ¼ë¡œ ë‚ ì§œë¥¼ ë¬¸ìì—´ë¡œ ë°˜í™˜í•˜ì—¬ í•˜ë£¨ê°€ ë°€ë¦¬ëŠ” ë¬¸ì œê°€ ë°œìƒí•  ìˆ˜ ìˆìœ¼ë¯€ë¡œ
  // ì—°ë„/ì›”/ì¼ì„ ì§ì ‘ ì¡°í•©í•˜ì—¬ "YYYY-MM-DD" í˜•ì‹ì˜ ë¬¸ìì—´ì„ ìƒì„±í•œë‹¤.
  const getDateString = (day: number) => {
    const year = currentDate.getFullYear();
    const month = currentDate.getMonth() + 1;
    const monthStr = month.toString().padStart(2, '0');
    const dayStr = day.toString().padStart(2, '0');
    return `${year}-${monthStr}-${dayStr}`;
  };

  const getReservationCount = (day: number) => {
    const dateString = getDateString(day);
    return daycareData[dateString]?.length || 0;
  };

  const isWeekend = (day: number) => {
    const year = currentDate.getFullYear();
    const month = currentDate.getMonth();
    const date = new Date(year, month, day);
    return date.getDay() === 0 || date.getDay() === 6;
  };

  const getStatusColor = (count: number, isWeekendDay: boolean) => {
    if (isWeekendDay) return 'bg-gray-200 text-gray-500';
    if (count === 0) return 'bg-gray-100 text-gray-500';
    if (count <= 2) return 'bg-green-100 text-green-700';
    if (count <= 4) return 'bg-yellow-100 text-yellow-700';
    return 'bg-red-100 text-red-700';
  };

  const getStatusText = (count: number, isWeekendDay: boolean) => {
    if (isWeekendDay) return 'íœ´ë¬´';
    if (count === 0) return 'ì˜ˆì•½ì—†ìŒ';
    if (count <= 2) return 'ì—¬ìœ ';
    if (count <= 4) return 'ë³´í†µ';
    return 'í¬í™”';
  };

  const navigateMonth = (direction: 'prev' | 'next') => {
    const newDate = new Date(currentDate);
    if (direction === 'prev') {
      newDate.setMonth(newDate.getMonth() - 1);
    } else {
      newDate.setMonth(newDate.getMonth() + 1);
    }
    setCurrentDate(newDate);
    setSelectedDate(null);
    setShowAddForm(false);
    setNewReservation({ petName: '', ownerName: '', time: '', phone: '' });
  };

  const handleDateClick = (day: number) => {
    if (isWeekend(day)) return; // ì£¼ë§ì€ í´ë¦­ ë¶ˆê°€
    const dateString = getDateString(day);
    setSelectedDate(selectedDate === dateString ? null : dateString);
    setShowAddForm(false);
    setNewReservation({ petName: '', ownerName: '', time: '', phone: '' });
  };

  const getReservationStatusColor = (status: string) => {
    switch (status) {
      case 'confirmed': return 'bg-green-100 text-green-800';
      case 'pending': return 'bg-yellow-100 text-yellow-800';
      case 'completed': return 'bg-blue-100 text-blue-800';
      case 'cancelled': return 'bg-red-100 text-red-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  // ìƒˆ ì˜ˆì•½ í¼ ì…ë ¥ í•¸ë“¤ëŸ¬
  const handleNewChange = (field: keyof typeof newReservation, value: string) => {
    setNewReservation((prev) => ({ ...prev, [field]: value }));
  };

  // ë°ì´ì¼€ì–´ ì˜ˆì•½ ì¶”ê°€
  const addReservation = () => {
    if (!selectedDate) return;
    const reservations = JSON.parse(localStorage.getItem('daycareReservations') || '[]');
    const newRes: any = {
      id: `daycare-${selectedDate}-${Date.now()}`,
      petName: newReservation.petName || 'ë°˜ë ¤ë™ë¬¼',
      ownerName: newReservation.ownerName || 'ê³ ê°',
      date: selectedDate,
      time: newReservation.time || 'ë¯¸ì •',
      phone: newReservation.phone || '',
      status: 'confirmed',
      service: 'daycare'
    };
    reservations.push(newRes);
    localStorage.setItem('daycareReservations', JSON.stringify(reservations));
    setNewReservation({ petName: '', ownerName: '', time: '', phone: '' });
    setShowAddForm(false);
    loadDaycareData();
    window.dispatchEvent(new CustomEvent('reservationUpdated'));
  };

  // ë°ì´ì¼€ì–´ ì˜ˆì•½ ì‚­ì œ
  const deleteReservation = (id: string) => {
    const reservations = JSON.parse(localStorage.getItem('daycareReservations') || '[]');
    const newData = reservations.filter((r: any) => r.id !== id);
    localStorage.setItem('daycareReservations', JSON.stringify(newData));
    loadDaycareData();
    window.dispatchEvent(new CustomEvent('reservationUpdated'));
  };

  const monthNames = ['1ì›”','2ì›”','3ì›”','4ì›”','5ì›”','6ì›”','7ì›”','8ì›”','9ì›”','10ì›”','11ì›”','12ì›”'];
  const dayNames = ['ì¼','ì›”','í™”','ìˆ˜','ëª©','ê¸ˆ','í† '];
  const today = new Date().toISOString().split('T')[0];

  return (
    <div className="bg-white rounded-lg shadow-lg p-6">
      <div className="mb-6">
        <div className="flex items-center justify-between mb-4">
          <h3 className="text-2xl font-bold text-gray-900">
            <i className="ri-home-heart-line mr-2 text-orange-600"></i>
            ë°ì´ì¼€ì–´ ì˜ˆì•½ ë‹¬ë ¥
          </h3>
          <div className="flex items-center space-x-2">
            <button onClick={() => navigateMonth('prev')} className="p-2 rounded-lg border border-gray-300 hover:bg-gray-50 transition-colors cursor-pointer">
              <i className="ri-arrow-left-line text-gray-600"></i>
            </button>
            <h4 className="text-xl font-semibold text-gray-800 min-w-[120px] text-center">
              {currentDate.getFullYear()}ë…„ {monthNames[currentDate.getMonth()]}
            </h4>
            <button onClick={() => navigateMonth('next')} className="p-2 rounded-lg border border-gray-300 hover:bg-gray-50 transition-colors cursor-pointer">
              <i className="ri-arrow-right-line text-gray-600"></i>
            </button>
          </div>
        </div>
        {/* ë²”ë¡€ */}
        <div className="flex items-center justify-center space-x-6 mb-6 p-4 bg-orange-50 rounded-lg">
          <div className="flex items-center space-x-2">
            <div className="w-4 h-4 bg-gray-200 rounded"></div>
            <span className="text-sm text-gray-700">íœ´ë¬´ (ì£¼ë§)</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-4 h-4 bg-gray-100 rounded"></div>
            <span className="text-sm text-gray-700">ì˜ˆì•½ì—†ìŒ</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-4 h-4 bg-green-100 rounded"></div>
            <span className="text-sm text-gray-700">ì—¬ìœ  (1-2ê±´)</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-4 h-4 bg-yellow-100 rounded"></div>
            <span className="text-sm text-gray-700">ë³´í†µ (3-4ê±´)</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-4 h-4 bg-red-100 rounded"></div>
            <span className="text-sm text-gray-700">í¬í™” (5ê±´+)</span>
          </div>
        </div>
      </div>
      {/* ë‹¬ë ¥ í—¤ë” */}
      <div className="grid grid-cols-7 gap-1 mb-2">
        {dayNames.map((day, index) => (
          <div key={day} className={`p-3 text-center font-semibold text-sm ${index === 0 ? 'text-red-600' : index === 6 ? 'text-blue-600' : 'text-gray-700'}`}>{day}</div>
        ))}
      </div>
      {/* ë‹¬ë ¥ ë³¸ì²´ */}
      <div className="grid grid-cols-7 gap-1">
        {getDaysInMonth().map((day, index) => {
          if (day === null) {
            return <div key={index} className="p-3"></div>;
          }
          const dateString = getDateString(day);
          const reservationCount = getReservationCount(day);
          const isToday = dateString === today;
          const isSelected = selectedDate === dateString;
          const isWeekendDay = isWeekend(day);
          return (
            <div key={day} onClick={() => handleDateClick(day)} className={`relative p-3 border border-gray-200 rounded-lg transition-all ${isWeekendDay ? 'bg-gray-100 cursor-not-allowed' : 'cursor-pointer hover:shadow-md'} ${isToday ? 'ring-2 ring-orange-500' : ''} ${isSelected ? 'bg-orange-50 border-orange-300' : !isWeekendDay ? 'hover:bg-gray-50' : ''}`}>
              <div className="text-center">
                <div className={`text-lg font-semibold mb-1 ${index % 7 === 0 ? 'text-red-600' : index % 7 === 6 ? 'text-blue-600' : isWeekendDay ? 'text-gray-400' : 'text-gray-900'}`}>{day}</div>
                <div className="space-y-1">
                  <div className={`text-xs px-2 py-1 rounded-full ${getStatusColor(reservationCount, isWeekendDay)}`}>{getStatusText(reservationCount, isWeekendDay)}</div>
                  {reservationCount > 0 && !isWeekendDay && <div className="text-xs text-gray-600">{reservationCount}ê±´ ì˜ˆì•½</div>}
                </div>
              </div>
              {isToday && <div className="absolute top-1 right-1"><div className="w-2 h-2 bg-orange-500 rounded-full"></div></div>}
            </div>
          );
        })}
      </div>
      {/* ì„ íƒëœ ë‚ ì§œ ìƒì„¸ ì •ë³´ */}
      {selectedDate && daycareData[selectedDate] && (
        <div className="mt-6 p-4 bg-orange-50 rounded-lg border border-orange-200">
          <h4 className="font-semibold text-orange-900 mb-3">
            {new Date(selectedDate).toLocaleDateString('ko-KR', { year: 'numeric', month: 'long', day: 'numeric', weekday: 'long' })} ë°ì´ì¼€ì–´ ì˜ˆì•½ í˜„í™©
          </h4>
          <div className="space-y-3">
            {daycareData[selectedDate].map((reservation, index) => (
              <div key={index} className="bg-white rounded-lg p-3 border border-orange-200">
                <div className="flex justify-between items-start">
                  <div className="flex-1">
                    <div className="flex items-center space-x-2 mb-1">
                      <span className="font-semibold text-gray-900">{reservation.time}</span>
                      <span className={`text-xs px-2 py-1 rounded-full ${getReservationStatusColor(reservation.status)}`}>{reservation.status === 'confirmed' ? 'í™•ì •' : reservation.status === 'pending' ? 'ëŒ€ê¸°' : reservation.status === 'completed' ? 'ì™„ë£Œ' : 'ì·¨ì†Œ'}</span>
                    </div>
                    <div className="text-sm text-gray-700">
                      <div className="flex items-center space-x-4">
                        <span><strong>ë°˜ë ¤ë™ë¬¼:</strong> {reservation.petName}</span>
                        <span><strong>ë³´í˜¸ì:</strong> {reservation.ownerName}</span>
                      </div>
                      <div className="mt-1">
                        <span><strong>ì—°ë½ì²˜:</strong> {reservation.phone}</span>
                      </div>
                    </div>
                  </div>
                  {/* ì‚­ì œ ë²„íŠ¼ */}
                  <button onClick={() => deleteReservation(reservation.id)} className="text-red-500 text-xs ml-2">ì‚­ì œ</button>
                </div>
              </div>
            ))}
          </div>
          {/* ì˜ˆì•½ ì¶”ê°€/í¼ */}
          <div className="mt-4">
            {showAddForm ? (
              <div className="space-y-2">
                <input type="text" placeholder="ë°˜ë ¤ë™ë¬¼ ì´ë¦„" value={newReservation.petName} onChange={(e) => handleNewChange('petName', e.target.value)} className="w-full border p-2 rounded" />
                <input type="text" placeholder="ë³´í˜¸ì ì´ë¦„" value={newReservation.ownerName} onChange={(e) => handleNewChange('ownerName', e.target.value)} className="w-full border p-2 rounded" />
                <input type="time" value={newReservation.time} onChange={(e) => handleNewChange('time', e.target.value)} className="w-full border p-2 rounded" />
                <input type="text" placeholder="ì—°ë½ì²˜" value={newReservation.phone} onChange={(e) => handleNewChange('phone', e.target.value)} className="w-full border p-2 rounded" />
                <div className="flex space-x-2">
                  <button onClick={addReservation} className="bg-orange-500 text-white px-3 py-1 rounded">ì¶”ê°€</button>
                  <button onClick={() => { setShowAddForm(false); setNewReservation({ petName: '', ownerName: '', time: '', phone: '' }); }} className="border px-3 py-1 rounded">ì·¨ì†Œ</button>
                </div>
              </div>
            ) : (
              <button onClick={() => setShowAddForm(true)} className="bg-orange-500 text-white px-3 py-1 rounded">ì˜ˆì•½ ì¶”ê°€</button>
            )}
          </div>
        </div>
      )}
      {/* ì˜ˆì•½ì´ ì—†ëŠ” ë‚ ì§œ */}
      {selectedDate && !daycareData[selectedDate] && !isWeekend(parseInt(selectedDate.split('-')[2])) && (
        <div className="mt-6 p-4 bg-gray-50 rounded-lg border border-gray-200">
          <h4 className="font-semibold text-gray-700 mb-2">
            {new Date(selectedDate).toLocaleDateString('ko-KR', { year: 'numeric', month: 'long', day: 'numeric', weekday: 'long' })}
          </h4>
          <p className="text-gray-600 mb-4">ì´ ë‚ ì€ ë°ì´ì¼€ì–´ ì˜ˆì•½ì´ ì—†ìŠµë‹ˆë‹¤.</p>
          <div>
            {showAddForm ? (
              <div className="space-y-2">
                <input type="text" placeholder="ë°˜ë ¤ë™ë¬¼ ì´ë¦„" value={newReservation.petName} onChange={(e) => handleNewChange('petName', e.target.value)} className="w-full border p-2 rounded" />
                <input type="text" placeholder="ë³´í˜¸ì ì´ë¦„" value={newReservation.ownerName} onChange={(e) => handleNewChange('ownerName', e.target.value)} className="w-full border p-2 rounded" />
                <input type="time" value={newReservation.time} onChange={(e) => handleNewChange('time', e.target.value)} className="w-full border p-2 rounded" />
                <input type="text" placeholder="ì—°ë½ì²˜" value={newReservation.phone} onChange={(e) => handleNewChange('phone', e.target.value)} className="w-full border p-2 rounded" />
                <div className="flex space-x-2">
                  <button onClick={addReservation} className="bg-orange-500 text-white px-3 py-1 rounded">ì¶”ê°€</button>
                  <button onClick={() => { setShowAddForm(false); setNewReservation({ petName: '', ownerName: '', time: '', phone: '' }); }} className="border px-3 py-1 rounded">ì·¨ì†Œ</button>
                </div>
              </div>
            ) : (
              <button onClick={() => setShowAddForm(true)} className="bg-orange-500 text-white px-3 py-1 rounded">ì˜ˆì•½ ì¶”ê°€</button>
            )}
          </div>
        </div>
      )}
    </div>
  );
}